import React, { useState } from 'react';

export default function EditDolares(props) {

    const [Valor, setValor] = useState("");

    function handleSubmit(e) {
        e.preventDefault();
        setValor(e.target.value);
    }
    function onSubmit(e) {
        e.preventDefault();
        props.edit(Valor);
    }

    return (
        <>
            <button type="button" className="btn btn-warning mt-2 mb-3" data-bs-toggle="modal" data-bs-target="#editDolar">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-pencil" viewBox="0 0 16 16">
                    <path d="M12.146.146a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1 0 .708l-10 10a.5.5 0 0 1-.168.11l-5 2a.5.5 0 0 1-.65-.65l2-5a.5.5 0 0 1 .11-.168l10-10zM11.207 2.5 13.5 4.793 14.793 3.5 12.5 1.207 11.207 2.5zm1.586 3L10.5 3.207 4 9.707V10h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.293l6.5-6.5zm-9.761 5.175-.106.106-1.528 3.821 3.821-1.528.106-.106A.5.5 0 0 1 5 12.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.468-.325z" />
                </svg>
            </button>
            <div className="modal fade" id="editDolar" tabIndex="-1" aria-labelledby="editModal" aria-hidden="true">
                <div className="modal-dialog">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="editModal">Nuevo Producto</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close">X</button>
                        </div>
                        <form>
                            <div className="modal-body">
                                <div className="form-group text-left">
                                    <label>Precio del dolar</label>
                                    <input className="form-control" name="dolar" onChange={handleSubmit} />
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Cerrar</button>
                                <button type="submit" onClick={onSubmit} className="btn btn-primary">Guardar</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    );
}
